.ChannelInfo {
    background-color: #302E2C;
    color: #2F4F4F;
    position: relative;
}

.ContainerChannelInfoisDisplay {
    width: 30%;
    height: 100%;
    display: block;
    opacity: 1;
}

.ContainerChannelInfoisReduce {
    width: 0%;
    opacity: 0;
    display: none;
}

.HeaderChannelInfo {
    background-color: #3E3B38;
    height: 60px;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
}

.ChannelInfoCard {
    background-color: white;
    padding: 10px;
    margin: 20px 0px;
    display: flex;
    justify-content: center;
    box-shadow: 0px 0px 3px black;
    cursor: pointer;
}

.HeaderChannelInfo h3 {
    margin: 0px;
}

.ChannelInfoCard>* {
    margin: 6px;
    padding: 0;
}

.ChannelName {
    flex-direction: column;
}

.SettingsButton h4 {
    display: flex;
    align-items: center;
}

.ChannelInfoCard .icon {
    font-size: 22px;
    color: #C7C7C7;
    margin: 0px 8px;
}

.clickable {
    cursor: pointer
}

.ContainerIsDisplay {
    display: block;
}

.ContainerIsReduce {
    display: none;
}

.leaveChannel {
    color: red;
    fill: red;
}

@media (max-width: 800px) {
    .ContainerChannelInfoisDisplay {
        width: 100%;
    }
}